"use client";

import NotificationItem from "./NotificationItem";
import { notificationsMock } from "./notificationsMock";

function groupByDay() {
  const today = new Date().toDateString();
  const yesterday = new Date(Date.now() - 86400000).toDateString();

  return {
    today: notificationsMock.filter(
      (n) => new Date(n.createdAt).toDateString() === today
    ),
    yesterday: notificationsMock.filter(
      (n) => new Date(n.createdAt).toDateString() === yesterday
    ),
    older: notificationsMock.filter((n) => {
      const d = new Date(n.createdAt).toDateString();
      return d !== today && d !== yesterday;
    }),
  };
}

export default function NotificationsFeed() {
  const groups = groupByDay();

  return (
    <div className="space-y-8">
      {groups.today.length > 0 && (
        <section>
          <h4 className="mb-3 text-sm font-semibold text-gray-500 dark:text-gray-400">
            Hoy
          </h4>
          <div className="space-y-3">
            {groups.today.map((n) => (
              <NotificationItem key={n.id} item={n} />
            ))}
          </div>
        </section>
      )}

      {groups.yesterday.length > 0 && (
        <section>
          <h4 className="mb-3 text-sm font-semibold text-gray-500 dark:text-gray-400">
            Ayer
          </h4>
          <div className="space-y-3">
            {groups.yesterday.map((n) => (
              <NotificationItem key={n.id} item={n} />
            ))}
          </div>
        </section>
      )}

      {groups.older.length > 0 && (
        <section>
          <h4 className="mb-3 text-sm font-semibold text-gray-500 dark:text-gray-400">
            Anteriores
          </h4>
          <div className="space-y-3">
            {groups.older.map((n) => (
              <NotificationItem key={n.id} item={n} />
            ))}
          </div>
        </section>
      )}
    </div>
  );
}
